@import url(https://fonts.googleapis.com/css?family=Lato:400,700,700i,900,900i);
.header {
  background-color: #1d1e2b;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 72px;
  color: #babcc5; }
  .header .header_logo {
    width: 38px;
    height: 32px; }
  .header a {
    color: #babcc5; }
  .header ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    margin-left: 150px;
    font-size: 21px;
    font-weight: bold; }
    .header ul li:not(:first-of-type) {
      border-left: 1px solid #babcc5; }
    .header ul li {
      padding: 0 30px;
      display: flex;
      align-items: center; }
  .header .social-media-icons .social-media-icon {
    fill: #babcc5; }
  .header .social-media-icons a {
    margin: 0 10px; }

button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 45px;
  background-color: #ff7250;
  color: #ffffff;
  font-size: 22px;
  font-style: bold;
  border: 1px solid;
  border-radius: 10px;
  margin-top: 40px; }
  button:hover {
    background-color: #f15a35;
    cursor: pointer; }

.intro {
  display: flex;
  justify-content: space-around;
  margin-top: 50px; }

.main_image {
  min-width: 432px;
  height: 432px;
  margin: 0 100px 50px 200px; }

.intro_text {
  margin-right: 200px; }

.intro-title {
  font-size: 96px;
  margin: 0 0 40px 0;
  font-weight: bold; }

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Lato, sans-serif;
  font-size: 16px;
  color: #1d1e2b;
  max-width: 100%;
  min-width: 700px; }

#root {
  min-height: 100%; }

button {
  outline: none; }

a {
  text-decoration: none; }

.logo-path {
  fill: #babcc5; }

